import { Component,OnInit  }        from '@angular/core';
import { ProseService }             from '../../shared/prose/prose.service';
import { Observable }               from 'rxjs/Observable';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Location }                 from '@angular/common';
import { NgClass }                  from '@angular/common';
import { Chapters }                 from './chapters';

@Component({
      moduleId: module.id,
      selector: 'chapters',
      templateUrl: 'chapter.component.html',
      styleUrls: ['chapter.component.css'],

})
export class ChapterComponent implements OnInit {

    errorMessage: string;
    stories: any[] = [];


    /**
     * Creates an instance of the ChapterComponent with the injected
     * ProseService.
     *
     * @param {ProseService} ProseService - The injected ProseService.
     */

      constructor(
          private proseService: ProseService,
          private route:ActivatedRoute,
          private location: Location
        ){}

        /**
         * Get the Stories
         */
        ngOnInit() {
          this.getStories();
        //   this.route.params
        //       .map(params => params['Chapters'])
        //       .subscribe((Chapters) => {
        //           this.proseService.getChapters(Chapters)
        //               .subscribe(Chapters => {
        //                  this.Chapters = Chapters;
        //               })
        //       })
        }
        /**
         * Handle the proseService observable
         */
        getStories() {
          this.proseService.get()
            .subscribe(
                Chapters => this.stories = Chapters,

                // stories => this.stories = stories,
                // chapter => console.log(chapter),
                error => this.errorMessage = <any>error
            );

        }
          goBack(): void {
             this.location.back();
  }
}



