import { Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { FirebaseListObservable, AngularFireDatabase } from 'angularfire2/database';
import '../../operators';
import { Observable } from 'rxjs/Observable';
import {AngularFireModule} from 'angularfire2';
import 'rxjs/add/operator/map';


@Injectable()

export class FirebaseService {
    tasks: FirebaseListObservable<Tasks[]>;
    stories: FirebaseListObservable<Stories[]>;


    constructor(private db: AngularFireDatabase) {

    }

    getTasks() {
        this.tasks = this.db.list('/tasks') as FirebaseListObservable<Tasks[]>;
        return this.tasks;

    }
    getStories() {
        this.stories = this.db.list('/stories') as FirebaseListObservable<Stories[]>;
        return this.stories;
    }
    // private extractData(res: Response) {
    //      const tasks = <Tasks[]>res.json().tasks;    // return array from json file
    //      return tasks || [];     // also return empty array if there is no data
    // }
}

export interface Tasks {
    $key?: string;
    title?: string;
    status?: boolean;
    description?: string;
    SLA?: string;
    startDate?: string;
    endDate?: string;
    createdBy?: string;
}

export interface Stories {
    $key?: string;
    StoryURL: string;
    title: string;
    Chapters: any[];

}



