import { NgModule } from '@angular/core';
import { ProseComponent } from './prose.component';
import { ProseRoutingModule } from './prose-routing.module';
import { SharedModule } from '../shared/shared.module';
import { ChapterModule } from './chapter/chapter.module';
import { ProseService } from '../shared/prose/prose.service';

@NgModule({
  imports: [ProseRoutingModule, SharedModule, ChapterModule],
  declarations: [ProseComponent],
  exports: [ProseComponent],
  providers: [ProseService]
})
export class ProseModule { }



